.mainmenu {
  .mainmenu__menu ul {
    @include clear;
    width: 64.28vw;
    margin: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    font-size: 0.94vw;
  }

  .mainmenu__menu ul li {
    text-decoration: none;

    margin-right: 5vw;
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;

    &:last-child {
      margin-right: 0;
    }

    a {
      border-bottom: get-vw-large(6px) solid transparent;
      color: $main;
      display: block;
      font-style: normal;
      font-weight: 700;
      font-size: 0.9375vw;
      line-height: 1.1979166666666667vw;
      padding: get-vw-large(23px) 0 get-vw-large(17px) 0;

      img {
        display: block;
      }
    }
  }

  .mainmenu__menu ul li:focus a,
  .mainmenu__menu ul li.active a,
  .mainmenu__menu ul li:hover a {
    border-color: #30ca4c;
  }

  li a img {
    width: 1.4583333333333333vw;
  }
}

.mainmenu__menu ul li.mainmenu__search {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  background: #fff;

