}

:root {
  scrollbar-color: #30ca4c #eaecf0;
  scrollbar-width: thin;
}

/* width */
::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #eaecf0;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #30ca4c;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #ccc;
}

$mobmenuheight: get-vw-small(116px);

.mobilemenu {
  position: absolute;
  top: $mobmenuheight;
  z-index: 999;
  left: -100%;
  -webkit-transition: all 0.4s linear;
  -o-transition: all 0.4s linear;
  transition: all 0.4s linear;
  display: none;
}

.mobilemenu.active {
  display: block;
  left: 0;
}

.mobilemenu__wrapper {
  padding: get-vw-small(51px) 0 get-vw-small(36px) 0;
  background: #fff;
  width: get-vw-small(260px);
  min-height: calc(100vh - #{$mobmenuheight});
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
}

.mobilemenu__count {
  display: inline-block;
  background: #f8444f;
  border-radius: 50%;
  color: #fff;
  font-style: normal;
  font-weight: 700;
  width: get-vw-small(12px);
  height: get-vw-small(12px);
  font-size: get-vw-small(10px);
  line-height: get-vw-small(13px);
  right: 0;
  text-align: center;
  top: 0;
  margin-left: get-vw-small(6px);
}

.mobilemenu__item {
