.contentform__input_topradio {
  /*   flex-wrap: wrap; */
  .radiogroup {
    /* width: 100%; */
    margin-bottom: get-vw-large(24px);
  }

  .customradio__wrapper {
    padding-right: get-vw-large(54px);
    position: relative;
  }

  .customradio__check {
    margin-left: 0 !important;
    position: absolute;
    right: get-vw-large(16px);
    top: 50%;
    transform: translateY(-50%);

    span {
      line-height: 1;
    }
  }
}

.customradio__unselected {
  display: block;
  width: get-vw-large(18px);
  height: get-vw-large(18px);
  max-width: get-vw-large(18px);
  min-width: get-vw-large(18px);
  border: get-vw-large(2px) solid #e1e4e7;
  border-radius: get-vw-large(555px);
}

.customradio input:checked ~ .customradio__icon .customradio__icn {
  opacity: 1;
}

.customradio input:checked ~ .customradio__text {
  opacity: 1;
}

.customradio input:checked ~ .customradio__check .customradio__unselected {
  display: none !important;
}

.customradio__selected {
  display: none !important;

  img {
    width: get-vw-large(18px);
    height: get-vw-large(18px);
  }
}

.customradio input:checked ~ .customradio__check .customradio__selected {
  display: block !important;
}

