    margin-top: get-vw-small(14px);
  }

  .sitefooter__link {
    margin: 0 get-vw-small(7px);

    a {
      display: block;
      font-size: get-vw-small(14px);
      line-height: get-vw-small(18px);
    }
  }

  .sitefooter__right {
    flex-wrap: wrap;
  }

  .sitefooter__copy {
    text-align: center;
    width: 100%;
    justify-content: center;
  }

  textarea {
    border-top-right-radius: get-vw-small(6px);
    border-top-left-radius: get-vw-small(6px);
    border: none;
    display: block;
    width: 100%;
    font-style: normal;
    font-weight: 500;

    color: #212531;
    font-style: normal;
    font-weight: 400;
    font-size: get-vw-small(16px);
    line-height: get-vw-small(20px);

    color: rgba(33, 37, 49, 0.7);
    margin-top: 0;
    margin-bottom: get-vw-small(30px);
  }
}

.searchtoggler {
  background: #f3f5f8;
  border-radius: 555px;
  padding: get-vw-large(6px) get-vw-large(12px) get-vw-large(6px)
    get-vw-large(14px);
  width: get-vw-large(105px);
}

.searchtoggler__items.dropdownblock {
  background: #f3f5f8;
  top: calc(100% - 16px);
  padding: get-vw-large(16px) 0 get-vw-large(6px) get-vw-large(14px);
  left: 0;
  border-bottom-left-radius: get-vw-large(10px);
  border-bottom-right-radius: get-vw-large(10px);
  width: 100%;
  min-width: unset;
}

.searchtoggler__button.fdropdown__button {
  position: relative;
  z-index: 9;
  line-height: 1;
}

.searchtoggler__items.dropdownblock {
  z-index: 8;
}

.searchtoggler__item {
  margin-bottom: get-vw-large(5px);
}

.searchtoggler__button span {
  font-style: normal;
  font-weight: 400;
  font-size: get-vw-large(16px);
  line-height: 100%;

  color: #212531;
}

.searchtoggler__item label {
  cursor: pointer;
  font-style: normal;
  font-weight: 400;
  font-size: get-vw-large(16px);
  line-height: 100%;
}

.searchtoggler__item input {
  opacity: 0;
}
$dropdownblock_top: get-vw-large(11px);

.searchtoggler {
  /* top: calc(50% + #{$dropdownblock_top}); */
  top: 50%;
  transform: translateY(-50%);
}
.searchtoggler {
  position: absolute;
  z-index: 99;
  left: get-vw-large(59px);
  
}

.searchinput input {
  background-size: 1.5vw;
}

.searchinput input {
  padding-left: 9.125vw;
}

@include medium-screen {
  .searchtoggler {
    padding: get-vw-medium(6px) get-vw-medium(12px) get-vw-medium(6px)
      get-vw-medium(14px);
    width: get-vw-medium(105px);
  }

  .searchtoggler__items.dropdownblock {
    top: calc(100% - 16px);
    padding: get-vw-medium(16px) 0 get-vw-medium(6px) get-vw-medium(14px);
    border-bottom-left-radius: get-vw-medium(10px);
    border-bottom-right-radius: get-vw-medium(10px);
  }

  .searchtoggler__item {
    margin-bottom: get-vw-medium(5px);
  }

  .searchtoggler__button span {
    font-size: get-vw-medium(16px);
  }

  .searchtoggler {
    left: get-vw-medium(39px);
  }
  .searchtoggler label {
    font-style: normal;
    font-weight: 400;
    font-size: get-vw-medium(16px);
    line-height: 100%;
  }

  .searchinput input {
    background-size: 2.8vw;
  }

  .searchinput input {
    padding-left: 18.125vw;
  }
}
@include small-screen {
  .pagesearch {
    display: none;
  }
}
